%pt-sans-font-normal-style {
  font-family: PT Sans;
  font-style: normal;
}

.user-card-container {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 140px;
  padding: 10px;
  background: #EAECF9;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 2px;

  .user-photo {
    width: 120px;
    height: 120px;
    border-radius: 120px;
  }
  .user-info-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding-left: 10px;

    .user-name {
      margin-bottom: 2px;
      font-weight: bold;
      font-size: 13px;
      line-height: 17px;
      color: #000000;
      @extend %pt-sans-font-normal-style;
    }

    .user-info-fields-container {
      text-align: left;

      .user-info-field {
        display: flex;
        margin-bottom: 2px;

        dt {
          @extend %pt-sans-font-normal-style;
          font-weight: normal;
          font-size: 10px;
          line-height: 13px;
          color: rgba(0, 0, 0, 0.6);
        }
        dd {
          @extend %pt-sans-font-normal-style;
          font-weight: bold;
          font-size: 10px;
          line-height: 13px;
          color: #000000;
          margin-left: 3px;
        }
      }
    }
  }
}
